.main-wrap{
vertical-align: top;
text-align: left;

}
em{
    background-color: yellow;
}
.category-wrap{
    vertical-align: top;

 text-align: left;
 display: inline-block;
 width:250px;
 background-color: #efefef;
 border:1px solid #ccc;
 max-height: 85vh;
 overflow: auto;
}
.dataitems-wrap{
    vertical-align: top;
    max-height: 85vh;
    overflow: auto;
    padding: 0 5px;
    text-align: left;
    display: inline-block;
    width:calc(100% - 300px);
}
.search-wrap{
    width:calc(100% - 10px);

}
.dataitems{
    padding:5px 0;
    margin-bottom:5px;
    border-bottom:1px solid #eee;
}
.dataitems:hover{
    background-color: #fcfcfc;
}
.dataitems:nth-child(even){
    background-color:#f6f6f6;

}
.dataitems .txt{
    padding-bottom:5px;

}
.datahdr{
    text-align: left;

}
.dataitems .cat{
    display: inline-block;
    width: 30px;
    text-align: center;
    border: 1px solid #ccc;
    font-size: smaller;
}
.dataitems .num{
    display: inline-block;
    width: 30px;
    text-align: center;
    border: 1px solid #ccc;
    font-size: smaller;


}
.category{
    padding:5px 4px;
    border-bottom:1px #fff groove; 


}
.category:hover{
    background-color: #ccc;

}
.dataitems-val{
  border:1px dashed #999;
  display: inline-block;
  border-radius: 4px;
  padding: 2px 8px;
  margin :1px 4px;
  background-color: #efefef;;
}